<template>
  <div style="position:relative;height: 100%">
    <el-row style="text-align:left;background: #fff;padding-top: 10px">
<!--      <el-col :span="6">-->
<!--        <el-input style="width: 300px;margin-right: 0px" placeholder="输入商品名查询(不输入内容默认搜索全部)" v-model="goodName"></el-input>-->
<!--        <el-button @click="findListClick">搜索</el-button>-->
<!--      </el-col>-->
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%;height: 90%">
      <el-table-column
        label="ID"
        type="index">
      </el-table-column>
      <el-table-column
        label="商品名"
        prop="goodName">
      </el-table-column>
      <el-table-column
        label="商品类型"
        prop="goodType">
      </el-table-column>
      <el-table-column
        label="数量"
        prop="buyNum">
      </el-table-column>
      <el-table-column
        label="采购地点"
        prop="buyPlace">
      </el-table-column>
      <el-table-column
        label="需要时间"
        prop="needTime">
      </el-table-column>
      <el-table-column
        label="购买状态"
        prop="status">
      </el-table-column>
      <el-table-column
        align="right">
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.status === '待采购'"
            size="mini"
            type="warning"
            @click="handleChange(scope.$index, scope.row)">采购</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="height: 30px;text-align: center">
      <pagination :currentPage="pageNum" :total=total :limit="pageSize" :small="false"
                  @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" style="text-align: center"/>
    </div>

  </div>
</template>

<script>
  import Pagination from './pagination.vue'
  import {commoditypurchaseListone,
    commoditypurchaseupdateStatus
  } from '@/api/user.js';
  export default {
    name: "result",
    components: {
      Pagination
    },
    data() {
      return {
        value1: '',
        tableData: [],
        search: '',
        pageNum: 1,
        pageSize: 10,
        total: 0,
        count: '',
        value3:'',
        parms:{
          pageNum:1,
          pageSize:10
        },
        selectList:{
          userId:'',
          status:null
        },
        value:null,
        goodName:'',
        dialogFormVisible: false,
        dialogFormVisibletwo:false,
        form: {
          goodName:'',
          goodType:'',
          window:'',
          buyNum:null,
          buyPrice:null,
          buyTime:null,
          status:'',
          id:'',
          principal:''
        },
        formLabelWidth: '120px'
      }
    },
    mounted() {
      this.commoditypurchaseListone();
    },
    methods: {
      handleEidt(index, row) {
        console.log(index, row);
        this.form.id=row.id
        this.form.goodName=row.goodName
        this.form.goodType=row.goodType
        this.form.window=row.window
        this.form.buyNum=row.buyNum
        this.form.buyPrice=row.buyPrice
        this.form.buyTime=row.buyTime
        this.form.status=row.status
        this.form.principal=row.principal
        this.dialogFormVisibletwo = true;
        this.commoditypurchaseListone();

      },
      handleChange(index, row){
        commoditypurchaseupdateStatus(row.id,row.status).then((response)=>{
          if(response.data.code === 200){
            row.status = '已采购'
            this.$message.success('采购成功')
          }
        })
      },
      handleDelete(index, row) {
        console.log(index, row);
        var r=confirm("是否确定删除？");
        if (r===true){
          producetaskdeleteList(row.id).then((response)=>{
          })
          this.producetaskList();
        }else{

        }
      },
      getSTime (val) {
        console.log(val)
        this.value1 = val
        var d = new Date(this.value1)
        var startTime =
          d.getFullYear() +
          '-' +
          (d.getMonth() + 1) +
          '-' +
          d.getDate() +
          ' ' +
          d.getHours() +
          ':' +
          d.getMinutes() +
          ':' +
          d.getSeconds()
        console.log(startTime)
        this.form.buyTime = startTime
      },
      handleCurrentChange (val) {
        console.log(val)//页码
        this.parms.pageNum = val
        this.commoditypurchaseListone();
      },
      handleSizeChange (val) {
        console.log(val)
        this.parms.pageSize = val//页数
        this.commoditypurchaseListone();
      },

      //查询所有采购记录;
      commoditypurchaseListone(){
        commoditypurchaseListone(this.parms).then((response)=>{
          this.tableData = response.data.obj.pageData
          this.total = response.data.obj.total
        })
      },
      exittwo(form){
        this.dialogFormVisibletwo = false
        this.resetFormData()
      },
      resetFormData(){
        this.form.goodsName ='';
        this.form.goodType ='';
        this.form.goodsPlace ='';
        this.form.buyNum ='';
        this.form.buyPrice ='';
        this.form.buyTime ='';
        this.form.status ='';
        this.form.principal ='';
      },
      exit(form){
        this.dialogFormVisible = false
        this.resetFormData()
      },
      closeDialog(done){
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
            location.reload();
          })
          .catch(_ => { });
      }
    },
  }
</script>

<style scoped>

</style>



